import {mapMutations} from 'vuex'
import ajax from '../../utils/ajax.js'
export default {
	data() {
		return {
			goodInfo:{}
		}
	},
	async onLoad(options){
		/*
			1.获取到query传参的goodId(
				1)可以通过onLoad的形参options获得
				2)mounted中可以通过this.$mp.query获得
				)
			2.通过该ID获取对应商品的详细信息
			3.展示数据
			
			功能:添加到购物车
				1.注册mutation用于同步修改state
				2.调用mutation,将商品数据传递给mutation
		*/
	   // console.log(options)
	   let {goodId} = options;
	   let goodInfo = await ajax('/getGoodDetail',{
		   goodId
	   })
	   this.goodInfo=goodInfo;
	   
	   // console.log(goodInfo)
	},
	// mounted(options){
	//    console.log(options,this)
	// }
	methods:{
		...mapMutations(["addShopItem"]),
		addToCart(){
			this.addShopItem(this.goodInfo);
		}
	}
}
